<template>
  <div>
    <form  @submit.prevent="submit">
      <div class="row justify-content-center">
        <div class="col-sm-9 p-2" style="font-size:12px;">
          <div class="shadow-sm p-3 rounded-lg">
          <!-- INPUT -->
          <div class="sm-form">
            <input
              type="text"
              id="id"
              name="id"
              class="form-control form-control-sm"
              placeholder="id"
              v-model="vdata.id"
              disabled
            />
          </div>
          <div class="row">
            <div class="col-6">
                <div class="sm-form ">
                    <label for="posisi">posisi</label>
                    <input type="text" id="posisi" name="posisi" class="form-control form-control-sm" placeholder="posisi/jabatan" v-model="vdata.posisi" >
                </div>
                <div class="sm-form ">
                    <label for="tempat_kerja">tempat_kerja</label>
                    <input type="text" id="tempat_kerja" name="tempat_kerja" class="form-control form-control-sm" placeholder="tempat_kerja" v-model="vdata.tempat_kerja" >
                </div>
            </div>
            <div class="col-6">
              <div class="sm-form ">
                  <label for="Tanggal">Tanggal</label>
                  <input type="date" id="Tanggal" name="Tanggal" class="form-control form-control-sm" placeholder="Tanggal" v-model="vdata.Tanggal" >
              </div>
              <div class="sm-form ">
                  <label for="lama_kerja">lama_kerja</label>
                  <input type="text" id="lama_kerja" name="lama_kerja" class="form-control form-control-sm" placeholder="lama_kerja" v-model="vdata.lama_kerja" >
              </div>
            </div>
          </div>
          <p class="p-3 pl-0">
            Keterangan
          </p>
          <wysiwyg v-model='vdata.keterangan' :options="{image: {  uploadURL: 'https://gis.officialapp.my.id/upload1.php',  dropzoneOptions: {}}}"/>

         <div class="sm-form ">
             <label for="urutan">urutan</label>
             <input type="number" id="urutan" name="urutan" class="form-control form-control-sm" placeholder="urutan" v-model="vdata.urutan" >
         </div>
          <!-- END INPUT -->
          <!-- BUTTON PART -->
          <div class="mt-3 p-3">
            <button
              type="submit"
              class="btn btn-sm"
              :class="
                btn == 'tambah'
                  ? 'btn-primary'
                  : btn == 'update'
                  ? 'btn-success'
                  : 'btn-danger'
              "
            >
              <span class="typcn typcn-info"></span> Proses  {{ btn }}
            </button>
            <div class="float-right">
              <button
                type="button"
                v-show="btn!='tambah'"
                @click="
                  btn = 'tambah';
                  vdata = {};
                "
                class="btn btn-sm btn-dark"
              >
                <span class="typcn typcn-edit"></span> Tambah
              </button>
              <button
                type="button"
                @click="btn = 'delete'"
                v-show="btn!='tambah'"
                class="ml-3 btn btn-sm btn-danger"
              >
                <span class="typcn typcn-delete"></span> Delete
              </button>
            </div>
          </div>
          </div>
          <!-- END BUTTON PART -->
        </div>
        <div class="col-sm-9 p-2" style="font-size:12px;">
          <div class="shadow-sm rounded-lg">
            <!-- TABLE PART -->
            <div v-if="datanya.length>0" class="p-2">
             <div class="text-right">
                <vue-json-excel :data="datanya">
                  <button type="button" class="btn btn-sm btn-dark">
                    Export <span class="typcn typcn-chart-area"></span>
                  </button>
                </vue-json-excel>
              </div>
                <Btables :tables="datanya"  :cari="['']" :hide="['keterangan']" @selected="ambil" ref="btable" />
            </div>
            <!-- END TABLE PART -->
         </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
// TOTURIAL 
// ganti source autophp, ganti source component btable, ganti path table sdb
import autophp from "@/plugins/autophp.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let sdb = new autophp();
let urlnya ='https://gis.officialapp.my.id'
export default {
    layout:'app_admin',
  components: {
    Btables,
  },
  data() {
    return {
      selected: false,
      vdata: {},
      btn: "tambah",
      datanya:[],
    };
  },
  methods: {
    getData(){
      sdb.collection("app_portofolio_pengalaman",false,urlnya).doc().select(`select * from app_portofolio_pengalaman where iduser='${this.$store.state.users.id}'`).then(res => {
        this.datanya=res;
        this.$forceUpdate();
      });
    },
    ambil(data) {
      this.vdata = data;
      this.btn = "update";
      this.$forceUpdate();
    },
    async submit() {
      let that=this
      that.vdata.iduser=this.$store.state.users.id
      if (confirm("Apakah yakin proses dilanjutkan ?")) {
        if (this.btn == "tambah") {
          delete this.vdata.id;
          await sdb
            .collection("app_portofolio_pengalaman",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "update") {
         await sdb
            .collection("app_portofolio_pengalaman",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "delete") {
          await sdb
            .collection("app_portofolio_pengalaman",true,urlnya)
            .doc(this.vdata.id)
            .delete()
              that.getData();
        }
      }
    },
  },
  async mounted() {
          this.getData();
  },
};
</script>